// Logo component
const LogoPlaceholder = ({ src, name, color, category }) => {
  if (src) {
    return (
      <img
        src={src}
        alt={name}
        className="w-24 h-24 object-contain grayscale opacity-80 group-hover:grayscale-0 group-hover:opacity-100 transition-all duration-300"
      />
    );
  }
  return (
    <div className={`w-24 h-24 rounded-2xl bg-gradient-to-br ${color} flex items-center justify-center text-white font-bold text-xl shadow-lg relative`}>
      {name.charAt(0)}
      <div className="absolute -top-2 -right-2 w-8 h-8 bg-white/20 rounded-full flex items-center justify-center text-sm">
        {category === 'Fintech' ? 'üí≥' : category === 'Telecom' ? 'üì±' : 'üè¢'}
      </div>
    </div>
  );
};

// Carrousel de logos premium
const LogoCarousel = () => {
  const [hoveredLogo, setHoveredLogo] = useState(null);
  const duplicatedLogos = [...clientLogos, ...clientLogos];
  
  return (
    <div className="relative w-full overflow-hidden">
      {/* Gradient masks pour effet de fondu */}
      <div className="absolute left-0 top-0 w-32 h-full bg-gradient-to-r from-gray-50 to-transparent z-10"></div>
      <div className="absolute right-0 top-0 w-32 h-full bg-gradient-to-l from-gray-50 to-transparent z-10"></div>
      
      <motion.div
        className="flex gap-16 py-8"
        animate={{ x: ['0%', '-50%'] }}
        transition={{
          ease: 'linear',
          duration: 40,
          repeat: Infinity,
        }}
      >
        {duplicatedLogos.map((logo, index) => (
          <motion.div
            key={`${logo.name}-${index}`}
            className="flex-shrink-0 group cursor-pointer relative"
            onHoverStart={() => setHoveredLogo(index)}
            onHoverEnd={() => setHoveredLogo(null)}
            whileHover={{ scale: 1.05, zIndex: 20 }}
            transition={{ type: 'spring', stiffness: 400, damping: 20 }}
          >
            {/* Logo container */}
            <div className="relative mb-4">
              <LogoPlaceholder {...logo} />
              
              {/* Glow effect */}
              <div className={`absolute inset-0 bg-gradient-to-br ${logo.color} rounded-2xl opacity-0 group-hover:opacity-20 blur-xl transition-opacity duration-300`}></div>
            </div>
            
            {/* Logo info */}
            <div className="text-center">
              <p className="font-bold text-gray-900 group-hover:text-blue-700 transition-colors duration-300 text-lg">
                {logo.name}
              </p>
              <p className="text-sm text-gray-600 mt-1 opacity-0 group-hover:opacity-100 transition-opacity duration-300">
                {logo.category}
              </p>
            </div>

            {/* Tooltip on hover */}
            {hoveredLogo === index && (
              <motion.div
                initial={{ opacity: 0, y: 10 }}
                animate={{ opacity: 1, y: 0 }}
                transition={{ duration: 0.2 }}
                className="absolute -bottom-24 left-1/2 transform -translate-x-1/2 bg-gray-900 text-white p-3 rounded-lg text-sm whitespace-nowrap shadow-xl z-30"
              >
                <div className="font-bold">{logo.name}</div>
                <div className="text-gray-300 text-xs mt-1 max-w-xs">{logo.description}</div>
                <div className="absolute -top-1 left-1/2 transform -translate-x-1/2 w-2 h-2 bg-gray-900 rotate-45"></div>
              </motion.div>
            )}
          </motion.div>
        ))}
      </motion.div>
    </div>
  );
};

// Carte de statistique premium
const PremiumStatCard = ({ value, label, suffix, color, icon, description }) => {
  const ref = useRef(null);
  const isInView = useInView(ref, { once: true, margin: "-50px" });
  const controls = useAnimation();
  const [displayValue, setDisplayValue] = useState(0);

  useEffect(() => {
    if (isInView) {
      controls.start({
        opacity: 1,
        y: 0,
        transition: { type: 'spring', stiffness: 200, damping: 15 }
      });
      
      const animationControls = animate(0, value, {
        duration: 1.5,
        ease: "easeOut",
        onUpdate: (latest) => {
          setDisplayValue(Number.isInteger(value) ? Math.round(latest) : parseFloat(latest.toFixed(1)));
        }
      });
      return () => animationControls.stop();
    }
  }, [isInView, value, controls]);

  return (
    <motion.div 
      ref={ref}
      className="relative group"
      initial={{ opacity: 0, y: 30 }}
      animate={controls}
      whileHover={{ y: -3 }}
      transition={{ type: 'spring', stiffness: 400, damping: 20 }}
    >
      {/* Background avec gradient */}
      <div className={`absolute inset-0 bg-gradient-to-br ${color} rounded-3xl opacity-90 group-hover:opacity-100 transition-opacity duration-300`}></div>
      
      {/* Glow effect */}
      <div className={`absolute inset-0 bg-gradient-to-br ${color} rounded-3xl blur-xl opacity-0 group-hover:opacity-30 transition-opacity duration-300`}></div>
      
      {/* Content */}
      <div className="relative p-8 text-center text-white">
        {/* Icon */}
        <div className="text-4xl mb-4 opacity-80 group-hover:opacity-100 transition-opacity duration-300">
          {icon}
        </div>
        
        {/* Value */}
        <div className="text-5xl lg:text-6xl font-bold mb-2 tracking-tight">
          {displayValue}{suffix}
        </div>
        
        {/* Label */}
        <div className="text-lg font-semibold mb-2 text-white/90">
          {label}
        </div>
        
        {/* Description */}
        <div className="text-sm text-white/70 group-hover:text-white/90 transition-colors duration-300">
          {description}
        </div>
        
        {/* Decorative elements */}
        <div className="absolute top-4 right-4 w-2 h-2 bg-white/30 rounded-full"></div>
        <div className="absolute bottom-4 left-4 w-1 h-1 bg-white/20 rounded-full"></div>
      </div>
    </motion.div>
  );
};

